import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, fetchEncryptStream, ObjectKeys, genNumId, parseFloatToDecimal, qryStore, generateComprobanteId } from "../../../stores/Utils";

import {  TextInput_Cmpt, SvgIcon_Cmpt, AccountLabelDetail_Cmpt } from "../../../components";


import "./styles.css"
import { CloseModal, OpenModal } from "../../../components/DialogHRM";
import {  getall_toPay } from "../services/server_fetching";
import { PickProvider_Cmpt } from "../../ComprobanteParametrizados";
import { useNavigate } from "react-router";
import { DateCalComponent_Cmpt } from "../../Inventory";
import ExtraExpenses from "./ExtraExpenses";
import { get_balances_by_type } from "../../CuentasContables/services/server_fetching";

const Svg = SvgIcon_Cmpt();

const TextInput = TextInput_Cmpt();



const DropInput = PickProvider_Cmpt();

const DateCalComponent =DateCalComponent_Cmpt();


const AccountLabelDetail = AccountLabelDetail_Cmpt();


const frmNm = "new_multi_pay_prov";
const Acc2Pay = "Acc2MPay";
const multi_pay_by_doc = "multi_pay_by_prov_Doc";
const toPay_by_doc = "toPay_by_docM";
const multi_pay_2S = "multi_pay_2S";

const _extraExp = "extraAccountListOnPay" 

const CobroManual  = ({ data, confirm }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const navigate = useNavigate();

	const cb_result = () => {
	
		set_obs(gen16CodeId())
	};
	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			get_balances_by_type("global_accounts","global_account_params",cb_result);
			updStore(multi_pay_2S, null);
			updStore(toPay_by_doc, null);
			updStore(multi_pay_by_doc, null);
			updStore("accountPicker", {});
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	let bankGlobal = getStore("global_account_params")?.cash_bank;


	const save  = async ()  => {
		let ffm = getStore(frmNm) || {};

		let comp2save = [];

		if(!ffm["date"]){
			ffm["date"] = (new Date()).getTime()+800;
		}
		else{
			ffm["date"] =(new Date(ffm["date"])).getTime();
		}

		let comprobanteId = await generateComprobanteId(ffm["date"]);
		ffm["comprobanteId"] = comprobanteId;

		let reference = ""

		let arr = getStore(multi_pay_2S) && ObjectKeys(getStore(multi_pay_2S));
		arr && arr?.map((flds, indE)=>{

			let nnfrm = {...ffm};
			let itm = getStore(multi_pay_2S)[flds];

			nnfrm["document"] =  itm?.document;
			reference += itm?.document +" ; ";
			nnfrm["account"] = itm?.account;
			nnfrm["subAccount"] = itm?.subAccount;
			nnfrm["providerId"] = itm?.providerId;
			nnfrm["type"] = itm?.type;
			nnfrm["amount"] = itm?.amount;
			nnfrm["payOrCollect"] = "pay";
			nnfrm["operationId"] = gen16CodeId();

			let bdyq2 = {...getStore(qryStore())?.add_opera};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form=nnfrm;

			
			comp2save.push(bdyq2)
			
		});

		
		
		
		let arrExtra = getStore(_extraExp) && ObjectKeys(getStore(_extraExp));
		arrExtra && arrExtra?.map((flds, indE)=>{

			let nnfrm = {...ffm};
			let itm = getStore(_extraExp)[flds];


		

			nnfrm["document"] = nnfrm["paymentDocument"];
			nnfrm["account"] = itm?.account;
			nnfrm["subAccount"] = itm?.subAccount;
			nnfrm["type"] = "debit";
			nnfrm["amount"] = itm?.amount;
			nnfrm["operationId"] = gen16CodeId();

			let bdyq2 = {...getStore(qryStore())?.add_opera};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form=nnfrm;
			comp2save.push(bdyq2)
			

			let nnfrm4 = {...ffm};

			nnfrm4["document"] = nnfrm4["paymentDocument"];
			nnfrm4["paymentDocument"]= reference;
			nnfrm4["account"] = bankGlobal?.account;
			nnfrm4["type"] = "credit";
			nnfrm4["amount"] =  itm?.amount*1;
			
			nnfrm4["operationId"] = gen16CodeId();

			let bdyq4 = {...getStore(qryStore())?.add_opera};
			bdyq4.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq4.form=nnfrm4;

			comp2save.push(bdyq4);
			
		});



		let nnfrm3 = {...ffm};
		let amount = SumCollects(getStore(multi_pay_2S));
		nnfrm3["document"] = nnfrm3["paymentDocument"];
		nnfrm3["paymentDocument"]= reference;
		nnfrm3["account"] = bankGlobal?.account;
		let accountDeb = getStore("account_ids")?.[nnfrm3["account"]];
		if(accountDeb.payOrCollect){
			nnfrm3["payOrCollect"] = accountDeb.payOrCollect;
		}
		nnfrm3["type"] = "credit";
		nnfrm3["amount"] = amount*1;
		
		nnfrm3["operationId"] = gen16CodeId();

		let bdyq2 = {...getStore(qryStore())?.add_opera};
		bdyq2.params = {
			businessId:  getStore("profileUser")?.businessId
		}
		bdyq2.form=nnfrm3;


		comp2save.push(bdyq2)

	
		const promises = comp2save.map(async (qry2Sv) => {
			const res = fetchEncryptStream(qry2Sv);
			const td = await res;
			if (td) {
				//console.log(td)
			}
        })      
        await Promise.all(promises);

		
		//data?.modalID && CloseModal({id:data?.modalID});


		
		setTimeout(() => {
			updStore(multi_pay_by_doc, null)
			updStore(frmNm, null)
			data?.modalID && CloseModal({id:data?.modalID});
			navigate({
				pathname: "/accounting-ledger" ,
				search: `?cId=${comprobanteId}`
			});
		
			
		}, 400);

		
		/**
		 * console.log(comp2save)
		 */
	
	};






	
	const add2F = () => {
		let frm2 = {...getStore(multi_pay_by_doc)} || {};
		let multi_pay_2S_frm = {...getStore(multi_pay_2S)} || {};
		ObjectKeys(frm2).map(fl=>{
			let kId =  getStore(Acc2Pay)?.account + "_"+getStore(Acc2Pay)?.subAccount+"_"+fl;
			let accDtls = {...getStore(Acc2Pay)};
			multi_pay_2S_frm[kId] = {};
			multi_pay_2S_frm[kId].amount = frm2[fl] * 1;
			multi_pay_2S_frm[kId].document = fl;
			multi_pay_2S_frm[kId].type = "debit";
			multi_pay_2S_frm[kId].account = accDtls?.account;
			multi_pay_2S_frm[kId].providerId = accDtls?.providerId;
			multi_pay_2S_frm[kId].subAccount = accDtls?.subAccount;
		})
		updStore(multi_pay_2S,multi_pay_2S_frm);
		updStore(multi_pay_by_doc,null);
		updStore(Acc2Pay,null);
		set_obs(gen16CodeId())
	};


	const openAddView =  () => {
		
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "93vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = <ProviderListingAccounts updObs={set_obs} confirm={add2F} />
		OpenModal(dataModal);
	}


	return (
		<div style={{
			maxHeight: "90vh",
			minHeight:  "88vh",
			width: "720px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Registrando Pago
				</p>
				<div className="flexSpace"/>
				
			</div>
				
				

			<div className="_dsplFlx"  >
				<div className="flexSpace"/>
				{ bankGlobal?.account?
					<div className=" acc_dtls">			
						<div className="_dsplFlx"  >
							<div className="code" style={{color: "#646cff"}}>
								{getStore("account_ids")?.[bankGlobal?.account]?.code} 
							</div>
							<div className="name">
							{getStore("account_ids")?.[bankGlobal?.account]?.name}
							</div>
						</div>
						<div className="flexSpace"/>
					</div>
					:null
				}
			</div>

					
				

			<div className="_dsplFlx ">
				<DateCalComponent init={getStore(frmNm)?.["date"]} updDate={(e)=>updFrm("date",e)} />
               
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["paymentDocument"]} 
					label={"Documento de Pago"} 
					width={200} 
					updChanges={(e)=>updFrm("paymentDocument",e)}
				/>
			</div>
			<TextInput  
				init={getStore(frmNm)?.["description"]} 
				label={"Descripcion"} 
				width={520} 
				updChanges={(e)=>updFrm("description",e)}
			/>


			<div className=" _dsplFlx" >
				
				<div className="flexSpace"/>
				<p className="group_title addBtn purple" onClick={openAddView}>
					Provedores
				</p>
			</div>


		{getStore(multi_pay_2S) && ObjectKeys(getStore(multi_pay_2S)).length>0?
		<div className="boxAddAcc" style={{margin: "18px 0"}}>

			<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
				<div className="tr_bar tr_bar_25">
					Cuenta
				</div>
				<div className="tr_bar tr_bar_25">
					Sub Cuenta
				</div>
				<div className="tr_bar tr_bar_25">
					Documento
				</div>
				<div className="tr_bar tr_bar_25">
					Balance
				</div>
			</div>
			{getStore(multi_pay_2S) && ObjectKeys(getStore(multi_pay_2S))?.map((element, indE)=>

				<ListingDocs 
					key={element} 
					idKey={element} 
					data={getStore(multi_pay_2S)[element]}
					updObs={set_obs}
				/>
			)}
			<ListingDocs 
					data={{
						account: bankGlobal?.account, 
						amount: SumCollects(getStore(multi_pay_2S)),
						"type":"credit",
						document: getStore(frmNm)?.["paymentDocument"]
					}}
					updObs={set_obs}
			/>
			
		</div>
		:null}

		<ExtraExpenses collectionName={_extraExp} />
			
			{getStore(frmNm)?.paymentDocument && SumCollects(getStore(multi_pay_2S))>0?
				<div className="_dsplFlx ">
					<div className="flexSpace"/>
				
					<div className="card">
						<button onClick={save}>
							Guardar
						</button>
					</div>
				</div>
			:null}

			
		</div>
	);
};

export default CobroManual;





const ListingDocs  = ({ data }) => {
	
	
	if(	data?.amount*1=== 0){
		return  null;
	}


	return (
		<div className="_dsplFlx hdr_bar">
			<div className="tr_bar tr_bar_25" style={{marginTop: 10}}>
				<AccountLabelDetail 
					data={getStore("account_ids")?.[data?.account]} 
					color={ data.type === "debit"? "#009688":"#c62828"}
					labelField={"code"}
					infoField={"name"}
				/>
			</div>
			<div className="tr_bar tr_bar_25" style={{marginTop: 10}}>
				{data?.subAccount?
				<AccountLabelDetail 
					data={getStore("sub_account_ids")?.[data?.subAccount]} 
					color={ data.type === "debit"? "#009688":"#c62828"}
					labelField={"subAccount"}
					infoField={"subAccountName"}
				/>
				: null}
			</div>
			<div className="tr_bar tr_bar_25" style={{marginTop: 10}}>
				{data?.document }
			</div>
			<div className="tr_bar tr_bar_25" style={{marginTop: 10}}>
				${(data?.amount*1).toFixed(2) }
			</div>
			
		</div>
	)

}















const ProviderListingAccounts  = ({ data, confirm }) => {


	const [obs, set_obs] = useState(0);

	const handleSearchAccount = (v) => {
		let nn= {
			"account":v?.account,
			"subAccount":v?.subAccount,
			providerId:v?.providerId
		};
		updStore(Acc2Pay, nn);
		getall_toPay(nn,cb_result);
	}


	
	const clearProvider = () => {
		updStore(Acc2Pay, null);
		//updStore(toPay_by_doc, null);
		set_obs(gen16CodeId())
	};

	const cb_result = () => {
	
		set_obs(gen16CodeId())
	};

	const add = () => {
		confirm && confirm();
		data?.modalID && CloseModal({id:data?.modalID});
	};


	


	
	return(
		<div style={{
			maxHeight: "92vh",
			minHeight:  "90vh",
			width: "720px",
			overflow: "auto",
			background: "#fff",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Pagos por Provedores
				</p>
				<div className="flexSpace"/>
				
			</div>
			<div  style={{margin: "18px 0"}}>
			{getStore(Acc2Pay)?.account ?
			<div>
				<div className=" acc_dtls">			
					<div className="_dsplFlx"  >
						<div className="code" style={{color: "#646cff"}} onClick={clearProvider}>
							{getStore("account_ids")?.[getStore(Acc2Pay)?.account]?.code} 
						</div>
						<div className="name">
						{getStore("account_ids")?.[getStore(Acc2Pay)?.account]?.name}
						</div>
					</div>
					<div className="flexSpace"/>
					{getStore(Acc2Pay)?.subAccount ?
					<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
						<div className="code" >
							{getStore("sub_account_ids")?.[getStore(Acc2Pay)?.subAccount]?.subAccount} 
						</div>
						<div className="name">
						{getStore("sub_account_ids")?.[getStore(Acc2Pay)?.subAccount]?.subAccountName} 
						</div>
					</div>
					:null}
				</div>
					<div className="boxAddAcc" style={{margin: "18px 0"}}>

						<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
							<div className="tr_bar tr_bar_50">
								Documento
							</div>
							<div className="tr_bar tr_bar_20">
								Balance
							</div>
							<div className="tr_bar tr_bar_5">
								
							</div>
							<div className="tr_bar tr_bar_25">
								Por Pagar
							</div>
						</div>
						{getStore(toPay_by_doc) && ObjectKeys(getStore(toPay_by_doc))?.map((element, indE)=>
						
							<ListingAccounts 
								key={element} 
								idKey={element} 
								data={getStore(toPay_by_doc)[element]}
								updObs={set_obs}
							/>
						)}
						
					</div>
				</div>
				:
				
				<DropInput
					placeholder={"buscar un provedor"} 
					width={420}
					handleClick={handleSearchAccount}
				/>
			}
		</div>
		{getStore(Acc2Pay)?.subAccount ?
		<div className="_dsplFlx ">
				<div className="flexSpace"/>
			
				<p className="group_title addBtn purple" onClick={add}>
						Guardar
				</p>
			</div>
		:null}
	</div>
	)

}






const ListingAccounts  = ({ data, updObs, idKey }) => {
	const [amount, set_amount] = useState(0);



	const handleChange = (v) => {
		set_amount(v);
		updFrm(data?.document,v)
		updObs(gen16CodeId())
	}
	
	
	
	const updFrm = (f, v, k) => {
		let frm2 = getStore(multi_pay_by_doc) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(multi_pay_by_doc, frm2);
	};

	
	if(	data?.amount*1=== 0){
		return  null;
	}


	return (
		<div className="_dsplFlx hdr_bar">
			<div className="tr_bar tr_bar_50" style={{marginTop: 10}}>
				{data?.document }
			</div>
			<div className="tr_bar tr_bar_20" style={{marginTop: 10}}>
				${(data?.amount*1).toFixed(2) }
			</div>
			<div className="tr_bar tr_bar_5" style={{marginTop: 10}}>
				{amount > data?.amount ? 
				<div className="icon" >
					<Svg name={"alert"} color={"#c62828"}/>
				</div>
				:null}
			</div>
		 
			<div className="tr_bar tr_bar_25">
				<TextInput  
					init={amount} 
					//label={"Descripcion"} 
					width={80} 
					updChanges={(e)=>handleChange(e)}
				/>
			</div>
			
		</div>
	)

}




const EstaCuadrado = (obj) => {
	let balance = 0;
	let arr = obj && ObjectKeys(obj);
	arr && arr?.map((element, indE)=>{
		let data = obj[element];
		let ammm =  parseFloatToDecimal(data?.amount,2);
		if(data?.type === "debit"){ 
			balance +=ammm
		}
		else if(data?.type === "credit"){ 
			balance -= ammm
		}
	});
	return arr && arr?.length>0 && balance === 0;
}  




const SumCollects = (obj) => {
	let balance = 0;
	obj && ObjectKeys(obj)?.map((flds, indE)=>{
		balance += parseFloatToDecimal(obj[flds]?.amount,2)*1;
	});
	return  balance;
}  





